:root {
  --content-height: 0px;
  --video-width: calc(100vw - 10px);
  --border-radius: 32px;
  --center-margin: 0px;
}

html, body {
  height: 100%;
  width: 100%;
  letter-spacing: 4px;
  color: #fff;
  margin: 0;
  font-family: Playfair Display SC;
  font-weight: 800;
}

body {
  background-color: #0a1113;
}

main {
  width: 100%;
  text-align: center;
}

h1 {
  margin: 0;
  font-size: 2em;
}

h2 {
  margin: 0;
  font-size: 1.25em;
}

.one {
  font-size: 1.4em;
  font-weight: 100;
}

a {
  width: fit-content;
  color: #fff;
  border-radius: var(--border-radius);
  margin: auto;
  text-decoration: none;
  transition: transform .1s;
}

a:hover {
  text-underline-offset: 4px;
  text-decoration: underline;
  transform: translateY(-2px);
}

a:active {
  transform: scale(.99);
}

iframe {
  transition: transform .1s;
}

iframe:hover {
  transform: translateY(-2px);
}

footer {
  width: auto;
  margin: 32px auto;
  padding-bottom: 80px;
}

.cover {
  width: 100%;
  height: 100%;
  position: fixed;
  overflow: hidden;
}

.background {
  z-index: -1;
}

.divider {
  width: 320px;
  border-top: 2px solid #fff;
  margin: 16px auto;
}

.video {
  border-radius: var(--border-radius);
  width: var(--video-width);
  height: calc(var(--video-width)  * .5625);
  margin: 5px;
}

.content {
  padding-top: var(--center-margin);
  width: 100%;
  height: 100%;
  text-align: center;
  margin: auto;
  position: static;
}

.capture, .capture > span {
  text-align: center;
  align-items: center;
}

#buttons > a {
  width: calc(100vw - 20px);
  margin: 5px 10px;
}

.button {
  border-radius: var(--border-radius);
  background-color: #0000005d;
  border: 2px solid #ffffffb3;
  margin: 5px;
  transition: border-color .2s ease-out;
}

.button:hover {
  border-color: #fff;
}

.fadein {
  opacity: 0;
  transition: opacity 1s, transform 1s;
  transform: translateY(32px);
}

.startFade {
  opacity: 1;
  transform: translateY(0);
}

#buttons {
  flex-direction: column;
  display: flex;
}

#sections {
  text-align: center;
}

#background * {
  height: 100%;
  width: 100%;
  object-fit: cover;
  margin: auto;
  position: absolute;
  inset: -9999px;
}

.credit {
  width: fit-content;
  color: #fff;
  border-radius: var(--border-radius);
  margin: auto;
  text-decoration: none;
  transition: transform .1s;
}

.credit:hover {
  text-underline-offset: 4px;
  text-decoration: underline;
  transform: translateY(-2px);
}

.credit:active {
  transform: scale(.99);
}

#overground {
  background-color: #000000b3;
}

#modal {
  pointer-events: none;
  opacity: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  text-align: center;
  background-color: #000000b3;
  padding-top: 80px;
  transition: opacity 1s, transform 1s, display 1s;
  position: fixed;
  overflow: auto;
}

#modal img {
  pointer-events: none;
  transition: opacity 1s, transform 1s;
  transform: translateY(32px);
}

@media screen and (min-width: 360px) {
  h1 {
    font-size: 2.5em;
  }

  h2 {
    font-size: 1.75em;
  }
}

@media screen and (min-width: 400px) {
  :root {
    --video-width: 400px;
  }

  .video {
    margin: auto;
  }
}

@media screen and (min-width: 420px) {
  .content {
    width: 400px;
  }

  #buttons > a {
    width: 400px;
    margin: 5px auto;
  }

  h1 {
    font-size: 3em;
  }

  h2 {
    font-size: 2em;
  }
}

@media screen and (min-width: 600px) {
  :root {
    --video-width: 530px;
  }

  h1 {
    font-size: 4em;
  }

  h2 {
    font-size: 3em;
  }

  .content {
    width: 600px;
  }
}

@media screen and (min-width: 1000px) {
  :root {
    --video-width: 720px;
  }

  .content {
    width: 1000px;
  }
}

@media screen and (min-width: 1000px) {
  :root {
    --video-width: 720px;
  }

  .content {
    width: 1000px;
  }

  @media screen and (max-height: 1080px) {
    .content {
      padding-top: calc(540px - var(--content-height)  * .5);
    }
  }
}

